.navbar{
    /* background-color:transparent; */
    background-color: rgb(48, 173, 75);
    color:black;
    height: 80px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position:static;
    /* top: -142px; */
    z-index: 999;
    width:100%;

}

.navbar-container{
    display: flex;
    justify-content: space-between;
    height: 80px;
    background-color: transparent;
}

.container{
    z-index: 1;
    width: 100%;
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    padding-left: 50px;
    padding-right: 50px;
}

.navbar-logo{
    color: #fff;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2.5rem;
     margin-right: auto;
    display:flex;
    align-items: center;
   

}

.navbar-icon{
    margin-right: 0.5rem;
    /* color: #000; */
}

.menu-item{
    display: flex;
}

.nav-menu{
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    text-align: center;
    justify-content: end;
}

.nav-item{
    height: 80px;
    border-bottom: 2px solid transparent;
}


.nav-item:hover{
    border-bottom: 2px solid #f00946;
  

    transition: all 0.2s ease;
}

.nav-links{
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    font-size: 1.5rem;
}

.fa-bars{
    color: #fff;
}

.menu-icon{
    display: none;
}

@media screen and (max-width: 960px){
    .NavbarItems{
        position:relative;
    }


.nav-menu{
     display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5 ease; 
}

.nav-menu.active{
    background: #1c2237;
    left: 0;
    opacity: 1;
    transition: all 0.6s ease;
    z-index: 1;
}

.nav-links{
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
}

.nav-links:hover{
    color: #f00946;
    transform: scale(1.2);

    transition: all 0.2s ease;
}

.nav-item:hover{
    border:none;
}

.nav-item{
    width: 100%;
}

.navbar-logo{
    
    position: absolute;
    top: 10px;
    left: 0;
    transform: translate(25%,50%);
}

.menu-icon{
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%,60%);
    font-size: 1.8rem;
    cursor: pointer;
}

.Fa-times{
    color: #fff;
    font-size: 2rem;
}

.nav-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;

}

}
.min{
    border-style: double;
}